// Sass vars*****

// Colors
$blue: #0F6FFF;
$blue-alt: #638BF2;
$pink: #dd1b87; // added for fun

$gray-400: #8B8B8B;
$gray-500: #767676;
$color-light: #fff;
$color-dark: #000;

$color-primary: $blue;
$color-primary-alt: $blue-alt;
$color-secondary: $pink; // added for fun - link hover

// Font family
$font-primary: 'Poppins', sans-serif; // 300, 400, 700, 800.

// Font weights
$font-weight-light: 300; // post category
$font-weight-regular: 400; // body text
$font-weight-bold: 700; // buttons, h2, post title
$font-weight-extra-bold: 800; // hero h1

// Sizes
$xxs: 450px;
$xs: 600px;
$sm: 768px;
$md: 1000px;
$md-lg: 1080px;
$lg: 1440px;

// Transitions
$speed-fast: 0.3s;
$speed-med: 0.5s;
$speed-slow: 1s;
$speed-really-slow: 1.5s;

// Shadow
$box-shadow: 0px 4px 4px rgba($color-dark, 0.25);
$box-shadow-secondary: 0px 9px 16px rgba($color-secondary, 0.25);

// Border radius
$border-radius-default: 4px; // post cards